<template>
  <div class="row items-center full-height">
    <div class="column items-center full-width">
      <q-card style="width: 500px">
        <q-card-section class="q-gutter-y-sm">
          <q-btn
            color="primary"
            label="Download scoring results"
            @click="download"
            class="full-width"
          />
          <q-btn
            outline
            label="Start New Session"
            @click="resetSession"
            class="full-width text-red"
          />
        </q-card-section>
      </q-card>
    </div>
  </div>
</template>

<script>
import { QCard, QBtn } from "quasar/dist/quasar.esm";

export default {
  name: "Download",
  components: { QCard, QBtn },
  props: ["images", "scores"],

  methods: {
    download() {
      var header = "Image";
      var csv = "";

      for (let i = 0; i < this.images.length; i++) {
        csv += this.images[i].name;
        for (let label in this.scores[i]) {
          if (i === 0) {
            header += "," + label;
          }
          csv += "," + this.scores[i][label];
        }
        csv += "\n";
      }

      header += "\n";

      const hiddenElement = document.createElement("a");
      hiddenElement.href =
        "data:text/csv;charset=utf-8," + encodeURI(header + csv);
      hiddenElement.target = "_blank";
      hiddenElement.download = "scoring_results.csv";
      hiddenElement.click();
    },
    resetSession() {
      this.$emit("reset");
    }
  }
};
</script>

<style scoped></style>
