<template>
  <div id="app">
    <Load @load="load" v-if="!loaded" />
    <Scoring
      v-if="loaded && !download"
      :images.sync="images"
      :current.sync="current"
      @scored="scored"
      @reset="checkReset = true"
    />
    <Download
      v-if="download"
      :images.sync="images"
      :scores.sync="scores"
      @reset="checkReset = true"
    />

    <q-dialog v-model="checkReset" persistent>
      <q-card>
        <q-card-section class="row items-center">
          <q-icon name="warning" class="text-red" style="font-size: 4rem;" />
          <span class="q-ml-sm">
            Do you really want to delete all of your current scoring
            progress?
          </span>
        </q-card-section>

        <q-card-actions align="right">
          <q-btn flat label="Cancel" color="primary" @click="checkReset = false" />
          <q-btn flat label="Delete" class="text-red" @click="reset" />
        </q-card-actions>
      </q-card>
    </q-dialog>
  </div>
</template>

<script>
import localforage from "localforage";

import Load from "./components/Load.vue";
import Scoring from "./components/Scoring.vue";
import Download from "./components/Download.vue";

export default {
  name: "App",
  components: {
    Load,
    Scoring,
    Download
  },

  data() {
    return {
      checkReset: false,
      download: false,
      loaded: false,
      images: [],
      scores: [],
      current: 0
    };
  },

  async mounted() {
    const session = await localforage.getItem("session");

    if (session) {
      this.images = session.images;
      this.current = session.current;
      this.scores = session.scores;
      this.download = this.images.length === this.scores.length;
      this.loaded = true;
    }
  },

  methods: {
    load(images) {
      this.images = images;
      this.current = 0;
      this.scores = [];
      this.loaded = true;
    },
    async scored(result) {
      this.scores[this.current] = result;

      if (this.images[this.current + 1]) {
        this.current++;
      }

      await localforage.setItem("session", {
        images: this.images,
        current: this.current,
        scores: this.scores
      });

      if (
        !this.images[this.current + 1] &&
        this.images.length === this.scores.length
      ) {
        this.download = true;
      }
    },
    async reset() {
      await localforage.removeItem("session");
      this.images = [];
      this.scores = [];
      this.current = 0;
      this.loaded = false;
      this.download = false;
      this.checkReset = false;
    }
  }
};
</script>

<style>
body,
#app {
  width: 100vw;
  height: 100vh;
  background: #f5f5f5;
}
</style>
