<template>
  <div class="full-height full-width q-px-md">
    <div class="row items-center full-height q-col-gutter-md">
      <div class="col-grow">
        <q-img :src="imgData" style="max-height: 95vh" contain />
      </div>
      <div class="column">
        <q-card>
          <q-card-section>
            <div class="text-h6">Scoring</div>
          </q-card-section>

          <q-separator />

          <q-card-section>
            <q-list class="q-gutter-y-sm">
              <q-item v-for="(options, label) in score" :key="label" class="no-padding">
                <q-item-section class="q-mb-lg">
                  <q-item-label class="q-mb-sm" overline>
                    {{
                    label
                    }}
                  </q-item-label>
                  <q-item-label>
                    <q-option-group
                      v-model="result[label]"
                      :options="options"
                      color="primary"
                      inline
                      dense
                    />
                  </q-item-label>
                </q-item-section>
              </q-item>
            </q-list>
          </q-card-section>

          <q-separator />

          <q-card-section class="q-gutter-y-sm">
            <q-btn color="primary" label="Next" @click="next" class="full-width" />
            <q-btn outline label="Reset session" @click="resetSession" class="full-width text-red" />
          </q-card-section>
        </q-card>
      </div>
    </div>
    <div class="progress full-width">
      <q-linear-progress stripe size="10px" :value="this.current / this.images.length" />
    </div>
  </div>
</template>

<script>
import {
  QBtn,
  QCard,
  QImg,
  QLinearProgress,
  QOptionGroup,
  QList,
  QItem,
  QItemSection,
  QItemLabel
} from "quasar/dist/quasar.esm";

export default {
  name: "Scoring",
  props: ["images", "current"],
  components: {
    QBtn,
    QCard,
    QImg,
    QLinearProgress,
    QOptionGroup,
    QList,
    QItem,
    QItemSection,
    QItemLabel
  },

  data() {
    return {
      imgData: null,
      score: {
        "Distanz: Branching bis Flossenende": [
          { label: ">1,50 mm", value: 0 },
          { label: "1,00-1,49 mm", value: 1 },
          { label: "0,50-0,99 mm", value: 2 },
          { label: "<0,50 mm", value: 3 }
        ],
        "Kollateralendichte proximale Hälfte": [
          { label: "keine", value: 0 },
          { label: "kaum", value: 1 },
          { label: "wenige", value: 2 },
          { label: "viele", value: 3 }
        ],
        "Kollateralendichte distale Hälfte": [
          { label: "keine", value: 0 },
          { label: "kaum", value: 1 },
          { label: "wenige", value: 2 },
          { label: "viele", value: 3 }
        ],
        "Anzahl dicker Kollateralen (doppelwandig)": [
          { label: "0", value: 0 },
          { label: "1", value: 1 },
          { label: "2-3", value: 2 },
          { label: ">3", value: 3 }
        ],
        "Kollateralen Regelmäßigkeit / Muster": [
          { label: "nicht erkennbar", value: 0 },
          { label: "teilweise", value: 1 },
          { label: "erkennbar", value: 2 },
          { label: "klar erkennbar", value: 3 }
        ]
      },
      result: {
        "Distanz: Branching bis Flossenende": null,
        "Kollateralendichte proximale Hälfte": null,
        "Kollateralendichte distale Hälfte": null,
        "Anzahl dicker Kollateralen (doppelwandig)": null,
        "Kollateralen Regelmäßigkeit / Muster": null
      }
    };
  },

  async mounted() {
    this.showImage(this.images[this.current]);
  },

  computed: {
    scoreComplete() {
      for (let field in this.result) {
        if (this.result[field] === null) {
          return false;
        }
      }

      return true;
    }
  },

  methods: {
    next() {
      if (this.scoreComplete) {
        this.$emit("scored", {
          ...this.result,
          Score: this.calculate()
        });

        const next = this.current + 1;
        if (this.images[next]) {
          this.reset();
          this.showImage(this.images[next]);
        }
      }
    },
    resetSession() {
      this.$emit("reset");
    },
    calculate() {
      return Object.values(this.result).reduce((sum, cur) => sum + cur);
    },
    reset() {
      this.result = {
        "Distanz: Branching bis Flossenende": null,
        "Kollateralendichte proximale Hälfte": null,
        "Kollateralendichte distale Hälfte": null,
        "Anzahl dicker Kollateralen (doppelwandig)": null,
        "Kollateralen Regelmäßigkeit / Muster": null
      };
    },
    showImage(img) {
      const reader = new FileReader();
      const self = this;
      reader.onload = function() {
        self.imgData = reader.result;
      };
      reader.readAsDataURL(img);
    }
  }
};
</script>

<style scoped>
.progress {
  position: absolute;
  left: 0;
  bottom: 0;
}
.q-option-group {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
}
</style>
