<template>
  <div class="row items-center full-height">
    <div class="column items-center full-width">
      <q-card style="width: 500px">
        <q-card-section>
          <q-file
            bottom-slots
            v-model="files"
            label="Pick images"
            outlined
            use-chips
            accept=".jpg, .png, .gif, .tif, image/*"
            multiple
            counter
          >
            <template v-slot:prepend>
              <q-icon name="cloud_upload" @click.stop />
            </template>

            <template v-slot:hint>Select images to be scored ...</template>
          </q-file>
          <br />
          <q-btn
            color="primary"
            label="Load scoring session"
            @click="loadSession"
            class="full-width"
            :disable="this.files.length <= 0"
          />
          <div class="q-field__bottom q-field__messages">
            <br />
            <center>
              Download App for offline usage:
              <a href="FishScoringApp.zip">download</a>
            </center>
          </div>
        </q-card-section>
      </q-card>
    </div>
  </div>
</template>

<script>
import { QCard, QFile, QIcon } from "quasar/dist/quasar.esm";

export default {
  name: "Load",
  components: { QCard, QFile, QIcon },
  props: ["images"],
  data() {
    return {
      files: []
    };
  },

  methods: {
    loadSession() {
      if (this.files.length > 0) {
        this.$emit("load", this.shuffleArray(this.files));
      }
    },
    shuffleArray(a) {
      var j, x, i;
      for (i = a.length - 1; i > 0; i--) {
        j = Math.floor(Math.random() * (i + 1));
        x = a[i];
        a[i] = a[j];
        a[j] = x;
      }
      return a;
    }
  }
};
</script>

<style scoped></style>
